@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.8.0/font/bootstrap-icons.css);
:root {
--clr-violet-indigo: #42275a;
--clr-eggplant: #734b6d;
--clr-dark-charcoal: #333333;
--clr-warm-yellow: #f2c94c;
--clr-aquamarine: #78ffd6;
--clr-pink-red: #d76d77;
--tr-1: 0.5s all ease;
}
html {
    scroll-behavior: smooth
}
#app {
    background: -webkit-gradient(linear, left top, left bottom, from(var(--clr-violet-indigo)), to(var(--clr-eggplant)));
    background: linear-gradient(to bottom, var(--clr-violet-indigo), var(--clr-eggplant));
    background-size: cover;
}
.full-height-width {
    height: 100%;
    width: 100%;
}
.in-middle {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.card {
    width: 75%;
    margin: 20vh 0 20vh 0;
    border: none;
    -webkit-transition: var(--tr-1);
    transition: var(--tr-1);
}
.card-body {
    padding: 8%;
}
.text-hover:hover {
    font-weight: bold;
    -webkit-transition: var(--tr-1);
    transition: var(--tr-1);
    cursor: pointer;
}


